import { useState, useEffect } from 'react'

interface Settings {
  subjects: string[]
  newSubject: string
}

export default function SettingsModal({ isOpen, onClose }: { isOpen: boolean; onClose: () => void }) {
  const [settings, setSettings] = useState<Settings>({
    subjects: JSON.parse(localStorage.getItem('subjects') || '[]') || [
      'Mathematics',
      'Physics',
      'Chemistry',
      'English',
      'History'
    ],
    newSubject: ''
  })
  const [uploading, setUploading] = useState(false)
  const [uploadMessage, setUploadMessage] = useState('')

  useEffect(() => {
    const savedSubjects = localStorage.getItem('subjects')
    if (savedSubjects) {
      setSettings(prev => ({ ...prev, subjects: JSON.parse(savedSubjects) }))
    }
  }, [isOpen])

  const addSubject = () => {
    if (settings.newSubject.trim() && !settings.subjects.includes(settings.newSubject)) {
      const updatedSubjects = [...settings.subjects, settings.newSubject]
      setSettings(prev => ({ ...prev, subjects: updatedSubjects, newSubject: '' }))
      localStorage.setItem('subjects', JSON.stringify(updatedSubjects))
    }
  }

  const removeSubject = (subject: string) => {
    const updatedSubjects = settings.subjects.filter(s => s !== subject)
    setSettings(prev => ({ ...prev, subjects: updatedSubjects }))
    localStorage.setItem('subjects', JSON.stringify(updatedSubjects))
  }

  const resetToDefault = () => {
    const defaultSubjects = ['Mathematics', 'Physics', 'Chemistry', 'English', 'History']
    setSettings(prev => ({ ...prev, subjects: defaultSubjects }))
    localStorage.setItem('subjects', JSON.stringify(defaultSubjects))
  }

  const deleteAllSubjects = () => {
    if (window.confirm('Are you sure you want to delete all subjects? You can add them back later.')) {
      setSettings(prev => ({ ...prev, subjects: [] }))
      localStorage.setItem('subjects', JSON.stringify([]))
    }
  }

  const handleExcelUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    setUploading(true)
    setUploadMessage('')

    try {
      const formData = new FormData()
      formData.append('file', file)

      const response = await fetch('http://localhost:5000/api/upload-schedule', {
        method: 'POST',
        body: formData
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Failed to upload schedule')
      }

      // Extract subjects from the schedule data
      const extractedSubjects = data.subjects || []
      
      if (extractedSubjects.length > 0) {
        // Combine with existing subjects and remove duplicates
        const combinedSubjects = Array.from(new Set([
          ...settings.subjects,
          ...extractedSubjects
        ]))
        
        setSettings(prev => ({ ...prev, subjects: combinedSubjects }))
        localStorage.setItem('subjects', JSON.stringify(combinedSubjects))
        
        // Store the schedule data
        if (data.todaySchedule) {
          localStorage.setItem('todaySchedule', JSON.stringify(data.todaySchedule))
        }
        if (data.tomorrowSchedule) {
          localStorage.setItem('tomorrowSchedule', JSON.stringify(data.tomorrowSchedule))
        }
        if (data.weekSchedule) {
          localStorage.setItem('weekSchedule', JSON.stringify(data.weekSchedule))
        }

        setUploadMessage(` Schedule uploaded successfully! Added ${extractedSubjects.length} subjects.`)
        
        // Clear message after 3 seconds
        setTimeout(() => setUploadMessage(''), 3000)
      }
    } catch (error) {
      setUploadMessage(` Error: ${error instanceof Error ? error.message : 'Unknown error'}`)
      console.error('Upload error:', error)
    } finally {
      setUploading(false)
      // Reset file input
      event.target.value = ''
    }
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-lg p-8 w-full max-w-md shadow-xl animate-slide-up max-h-[90vh] overflow-y-auto">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-2xl font-bold text-gray-800"> Settings</h2>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700 text-2xl font-bold"
          >
            
          </button>
        </div>

        {/* Subjects Management */}
        <div>
          <h3 className="text-lg font-semibold text-gray-800 mb-4"> Manage Subjects</h3>

          {/* Excel Upload Section - Optional */}
          <div className="mb-6 pb-6 border-b border-gray-200">
            <p className="text-sm text-gray-600 mb-3"> <span className="font-medium">Optional:</span> Import Subjects from Excel</p>
            <p className="text-xs text-gray-500 mb-3">
               Have a pre-made schedule? Upload an Excel file to automatically add all subjects at once. 
              Otherwise, just add subjects manually below.
            </p>
            <label className="flex items-center gap-2 px-4 py-3 bg-blue-50 border-2 border-blue-300 rounded-lg cursor-pointer hover:bg-blue-100 transition-colors">
              <input
                type="file"
                accept=".xlsx,.xls"
                onChange={handleExcelUpload}
                disabled={uploading}
                className="hidden"
              />
              <span className="text-blue-700 font-medium">
                {uploading ? ' Uploading...' : ' Choose Excel File (Optional)'}
              </span>
            </label>
            
            {uploadMessage && (
              <p className={`mt-2 text-sm p-2 rounded ${
                uploadMessage.includes('') 
                  ? 'bg-green-100 text-green-700' 
                  : 'bg-red-100 text-red-700'
              }`}>
                {uploadMessage}
              </p>
            )}
            
            <p className="text-xs text-gray-500 mt-2">
               Excel format: First row = day names (Monday-Sunday), First column = subject names
            </p>
          </div>

          {/* Add New Subject - Manual Entry */}
          <div className="mb-6 pb-6 border-b border-gray-200">
            <p className="text-sm text-gray-600 mb-3"> <span className="font-medium">Add Subjects Manually:</span></p>
            <p className="text-xs text-gray-500 mb-3">
              Start with the default subjects, or add/remove any as needed:
            </p>
            <div className="flex gap-2">
              <input
                type="text"
                placeholder="e.g., Biology, Music, Art"
                value={settings.newSubject}
                onChange={(e) => setSettings(prev => ({ ...prev, newSubject: e.target.value }))}
                onKeyPress={(e) => e.key === 'Enter' && addSubject()}
                className="input-field flex-1"
                autoComplete="off"
                spellCheck="false"
              />
              <button
                onClick={addSubject}
                className="btn-primary whitespace-nowrap"
              >
                Add
              </button>
            </div>
          </div>

          {/* Current Subjects */}
          <div className="mb-6">
            <p className="text-sm text-gray-600 mb-3">Your subjects:</p>
            <div className="space-y-2">
              {settings.subjects.length > 0 ? (
                settings.subjects.map(subject => (
                  <div
                    key={subject}
                    className="flex items-center justify-between bg-blue-50 p-3 rounded-lg border border-blue-200"
                  >
                    <span className="font-medium text-gray-700">{subject}</span>
                    <button
                      onClick={() => removeSubject(subject)}
                      className="text-red-600 hover:text-red-800 font-bold transition-colors"
                    >
                      
                    </button>
                  </div>
                ))
              ) : (
                <p className="text-gray-500 italic">No subjects added yet</p>
              )}
            </div>
          </div>

          {/* Reset Button */}
          <button
            onClick={resetToDefault}
            className="w-full py-2 px-4 bg-gray-300 text-gray-700 rounded-lg hover:bg-gray-400 font-medium transition-colors mb-4"
          >
            Reset to Default Subjects
          </button>

          {/* Delete All Button */}
          <button
            onClick={deleteAllSubjects}
            className="w-full py-2 px-4 bg-red-500 text-white rounded-lg hover:bg-red-600 font-medium transition-colors mb-4"
          >
            Delete All Subjects
          </button>
        </div>

        {/* Info Section */}
        <div className="bg-blue-50 p-4 rounded-lg border border-blue-200 mb-6">
          <p className="text-sm text-blue-900">
            <span className="font-semibold"> Tip:</span> Subjects you add here will appear in the homework form on the Calendar tab.
          </p>
        </div>

        {/* Close Button */}
        <button
          onClick={onClose}
          className="w-full py-2 px-4 bg-blue-600 text-white rounded-lg hover:bg-blue-700 font-medium transition-colors"
        >
          Close Settings
        </button>
      </div>
    </div>
  )
}
