import { useState, useEffect } from 'react'

interface Task {
  id: string
  text: string
  completed: boolean
  createdDate: string
}

export default function ChecklistTab() {
  // Load initial tasks from localStorage
  const [tasks, setTasks] = useState<Task[]>(() => {
    const savedTasks = localStorage.getItem('checklistTasks')
    if (savedTasks) {
      try {
        return JSON.parse(savedTasks)
      } catch {
        return []
      }
    }
    return []
  })
  const [newTask, setNewTask] = useState('')
  const [filter, setFilter] = useState<'all' | 'completed' | 'pending'>('all')

  // Save tasks to localStorage whenever they change
  useEffect(() => {
    localStorage.setItem('checklistTasks', JSON.stringify(tasks))
  }, [tasks])

  const addTask = () => {
    if (!newTask.trim()) return

    const task: Task = {
      id: Date.now().toString(),
      text: newTask,
      completed: false,
      createdDate: new Date().toLocaleDateString()
    }

    setTasks([...tasks, task])
    setNewTask('')
  }

  const toggleTask = (id: string) => {
    setTasks(tasks.map(task =>
      task.id === id ? { ...task, completed: !task.completed } : task
    ))
  }

  const deleteTask = (id: string) => {
    setTasks(tasks.filter(task => task.id !== id))
  }

  const filteredTasks = tasks.filter(task => {
    if (filter === 'completed') return task.completed
    if (filter === 'pending') return !task.completed
    return true
  })

  const completedCount = tasks.filter(t => t.completed).length
  const totalCount = tasks.length

  return (
    <div className="w-full max-w-4xl mx-auto">
      <h1 className="text-3xl font-bold text-gray-800 mb-6"> My Checklist</h1>

      {/* Progress Bar */}
      {totalCount > 0 && (
        <div className="mb-8 bg-white p-6 rounded-lg shadow-md">
          <div className="flex items-center justify-between mb-3">
            <span className="text-lg font-semibold text-gray-700">
              Progress: {completedCount}/{totalCount}
            </span>
            <span className="text-sm font-bold text-blue-600">
              {Math.round((completedCount / totalCount) * 100)}%
            </span>
          </div>
          <div className="w-full bg-gray-200 rounded-full h-3">
            <div
              className="bg-green-500 h-3 rounded-full transition-all duration-300"
              style={{ width: `${(completedCount / totalCount) * 100}%` }}
            ></div>
          </div>
        </div>
      )}

      {/* Add Task Form */}
      <div className="mb-8 bg-white p-6 rounded-lg shadow-md">
        <h2 className="text-xl font-semibold text-gray-800 mb-4">Add New Task</h2>
        <div className="flex gap-3">
          <input
            type="text"
            placeholder="Enter a new task..."
            value={newTask}
            onChange={(e) => setNewTask(e.target.value)}
            onKeyPress={(e) => e.key === 'Enter' && addTask()}
            className="input-field flex-1"
          />
          <button
            onClick={addTask}
            className="btn-primary"
          >
            + Add Task
          </button>
        </div>
      </div>

      {/* Filter Buttons */}
      <div className="mb-6 flex gap-2">
        {(['all', 'completed', 'pending'] as const).map(f => (
          <button
            key={f}
            onClick={() => setFilter(f)}
            className={`px-4 py-2 rounded-lg font-medium transition-colors ${
              filter === f
                ? 'bg-blue-600 text-white'
                : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
            }`}
          >
            {f.charAt(0).toUpperCase() + f.slice(1)}
          </button>
        ))}
      </div>

      {/* Tasks List */}
      <div className="space-y-3">
        {filteredTasks.length > 0 ? (
          filteredTasks.map(task => (
            <div
              key={task.id}
              className="bg-white p-4 rounded-lg shadow-md hover:shadow-lg transition-shadow flex items-start gap-4"
            >
              <input
                type="checkbox"
                checked={task.completed}
                onChange={() => toggleTask(task.id)}
                className="w-6 h-6 text-blue-600 rounded cursor-pointer mt-1"
              />
              <div className="flex-1">
                <p className={`text-lg font-medium ${
                  task.completed ? 'line-through text-gray-500' : 'text-gray-800'
                }`}>
                  {task.text}
                </p>
                <p className="text-sm text-gray-500 mt-1">
                  Added: {task.createdDate}
                </p>
              </div>
              <button
                onClick={() => deleteTask(task.id)}
                className="text-red-600 hover:text-red-800 font-medium transition-colors py-2 px-3 hover:bg-red-50 rounded"
              >
                Delete
              </button>
            </div>
          ))
        ) : (
          <div className="bg-gray-50 p-8 rounded-lg text-center">
            <p className="text-gray-500 text-lg">
              {tasks.length === 0
                ? 'No tasks yet. Add one to get started!'
                : `No ${filter} tasks to show.`}
            </p>
          </div>
        )}
      </div>

      {/* Summary Stats */}
      {tasks.length > 0 && (
        <div className="mt-8 grid grid-cols-3 gap-4">
          <div className="bg-blue-50 p-4 rounded-lg border-l-4 border-blue-500">
            <p className="text-gray-600 text-sm font-medium">Total Tasks</p>
            <p className="text-3xl font-bold text-blue-600">{totalCount}</p>
          </div>
          <div className="bg-green-50 p-4 rounded-lg border-l-4 border-green-500">
            <p className="text-gray-600 text-sm font-medium">Completed</p>
            <p className="text-3xl font-bold text-green-600">{completedCount}</p>
          </div>
          <div className="bg-yellow-50 p-4 rounded-lg border-l-4 border-yellow-500">
            <p className="text-gray-600 text-sm font-medium">Pending</p>
            <p className="text-3xl font-bold text-yellow-600">{totalCount - completedCount}</p>
          </div>
        </div>
      )}
    </div>
  )
}
