import { useState, useEffect } from 'react'

interface Resource {
  id: string
  name: string
  type: 'pdf' | 'image'
  data?: string // For small files stored as base64
  blobId?: string // For large files stored in IndexedDB
  uploadedDate: string
}

interface SubjectFolder {
  subject: string
  resources: Resource[]
}

// IndexedDB setup for large files
const DB_NAME = 'RoutineAppDB'
const STORE_NAME = 'resources'

const initDB = (): Promise<IDBDatabase> => {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, 1)
    request.onerror = () => reject(request.error)
    request.onsuccess = () => resolve(request.result)
    request.onupgradeneeded = (event) => {
      const db = (event.target as IDBOpenDBRequest).result
      if (!db.objectStoreNames.contains(STORE_NAME)) {
        db.createObjectStore(STORE_NAME)
      }
    }
  })
}

const saveBlob = async (blobId: string, data: string): Promise<void> => {
  try {
    const db = await initDB()
    const transaction = db.transaction(STORE_NAME, 'readwrite')
    const store = transaction.objectStore(STORE_NAME)
    store.put(data, blobId)
    return new Promise((resolve, reject) => {
      transaction.oncomplete = () => resolve()
      transaction.onerror = () => reject(transaction.error)
    })
  } catch (error) {
    console.error('Error saving to IndexedDB:', error)
  }
}

const getBlob = async (blobId: string): Promise<string | undefined> => {
  try {
    const db = await initDB()
    const transaction = db.transaction(STORE_NAME, 'readonly')
    const store = transaction.objectStore(STORE_NAME)
    return new Promise((resolve, reject) => {
      const request = store.get(blobId)
      request.onsuccess = () => resolve(request.result)
      request.onerror = () => reject(request.error)
    })
  } catch (error) {
    console.error('Error getting from IndexedDB:', error)
    return undefined
  }
}

const deleteBlob = async (blobId: string): Promise<void> => {
  try {
    const db = await initDB()
    const transaction = db.transaction(STORE_NAME, 'readwrite')
    const store = transaction.objectStore(STORE_NAME)
    store.delete(blobId)
    return new Promise((resolve, reject) => {
      transaction.oncomplete = () => resolve()
      transaction.onerror = () => reject(transaction.error)
    })
  } catch (error) {
    console.error('Error deleting from IndexedDB:', error)
  }
}

export default function ResourcesTab() {
  const [subjects, setSubjects] = useState<string[]>([])
  const [resourceFolders, setResourceFolders] = useState<SubjectFolder[]>([])
  const [selectedSubject, setSelectedSubject] = useState<string | null>(null)
  const [showModal, setShowModal] = useState(false)

  useEffect(() => {
    const savedSubjects = localStorage.getItem('subjects')
    const parsedSubjects = savedSubjects ? JSON.parse(savedSubjects) : []
    setSubjects(parsedSubjects)

    // Try to load from backend first, fall back to localStorage
    const loadResources = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/resources')
        if (response.ok) {
          const backendResources = await response.json()
          if (backendResources && backendResources.length > 0) {
            console.log(' Loaded resources from backend database')
            // Convert backend data to folder structure
            const folderMap = new Map<string, SubjectFolder>()
            backendResources.forEach((res: any) => {
              if (!folderMap.has(res.subject)) {
                folderMap.set(res.subject, { subject: res.subject, resources: [] })
              }
              folderMap.get(res.subject)!.resources.push({
                id: res.id,
                name: res.file_name,
                type: res.file_type,
                data: res.file_path,
                uploadedDate: res.upload_date
              })
            })
            const folders = Array.from(folderMap.values())
            setResourceFolders(folders)
            // Restore to localStorage for faster access
            localStorage.setItem('resourceFolders', JSON.stringify(folders))
            return
          }
        }
      } catch (err) {
        console.log('Backend unavailable, using localStorage')
      }

      // Fall back to localStorage
      const savedResources = localStorage.getItem('resourceFolders')
      let folders: SubjectFolder[] = []

      if (savedResources) {
        try {
          folders = JSON.parse(savedResources)
          console.log(' Loaded resources from localStorage')
        } catch {
          folders = parsedSubjects.map((s: string) => ({ subject: s, resources: [] }))
        }
      } else {
        folders = parsedSubjects.map((s: string) => ({ subject: s, resources: [] }))
      }

      const existingSubjects = new Set(folders.map(f => f.subject))
      parsedSubjects.forEach((s: string) => {
        if (!existingSubjects.has(s)) {
          folders.push({ subject: s, resources: [] })
        }
      })

      setResourceFolders(folders)
    }

    loadResources()
  }, [])

  useEffect(() => {
    if (resourceFolders.length > 0) {
      localStorage.setItem('resourceFolders', JSON.stringify(resourceFolders))
      
      // Also sync to backend database
      resourceFolders.forEach(folder => {
        if (folder.resources.length > 0) {
          fetch('http://localhost:5000/api/resources/save', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              subject: folder.subject,
              resources: folder.resources
            })
          }).catch(err => console.error('Error syncing resources:', err))
        }
      })
    }
  }, [resourceFolders])

  const handleAddResource = (files: File[]) => {
    if (!selectedSubject) return

    const validFiles = files.filter(f => {
      const isPdf = f.type === 'application/pdf' || f.name.toLowerCase().endsWith('.pdf')
      const isImage = f.type.startsWith('image/')
      const isValidType = isPdf || isImage
      const sizeLimit = 10 * 1024 * 1024 // 10MB limit
      const isValidSize = f.size < sizeLimit
      
      if (!isValidType) {
        console.warn(`Skipping ${f.name}: invalid file type`)
        return false
      }
      if (!isValidSize) {
        alert(`${f.name} is too large. Max 10MB`)
        return false
      }
      return true
    })

    if (validFiles.length === 0) {
      alert('No valid files selected')
      return
    }

    let completed = 0
    const newResources: Resource[] = []

    validFiles.forEach((file, index) => {
      const reader = new FileReader()

      reader.onerror = () => {
        console.error(`Error reading file: ${file.name}`)
        completed++
        if (completed === validFiles.length && newResources.length > 0) {
          addResourcesToFolder(newResources)
        }
      }

      reader.onload = async (e) => {
        try {
          const base64String = e.target?.result as string
          const blobId = `blob_${Date.now()}_${index}`
          const sizeInMB = base64String.length / (1024 * 1024)

          // Use IndexedDB for files larger than 1MB (to avoid localStorage bloat)
          if (sizeInMB > 1) {
            await saveBlob(blobId, base64String)
            const resource: Resource = {
              id: Date.now().toString() + index,
              name: file.name,
              type: file.type.startsWith('image') ? 'image' : 'pdf',
              blobId: blobId, // Store reference instead of data
              uploadedDate: new Date().toLocaleDateString()
            }
            newResources.push(resource)
          } else {
            // Keep small files as base64 in localStorage
            const resource: Resource = {
              id: Date.now().toString() + index,
              name: file.name,
              type: file.type.startsWith('image') ? 'image' : 'pdf',
              data: base64String,
              uploadedDate: new Date().toLocaleDateString()
            }
            newResources.push(resource)
          }
        } catch (error) {
          console.error('Error creating resource:', error)
        }
        
        completed++
        if (completed === validFiles.length) {
          if (newResources.length > 0) {
            addResourcesToFolder(newResources)
          }
        }
      }

      reader.readAsDataURL(file)
    })
  }

  const addResourcesToFolder = (resourcesToAdd: Resource[]) => {
    setResourceFolders(prev =>
      prev.map(folder =>
        folder.subject === selectedSubject
          ? { ...folder, resources: [...folder.resources, ...resourcesToAdd] }
          : folder
      )
    )
    setShowModal(false)
  }

  const handleDeleteResource = async (subjectName: string, resourceId: string) => {
    // Delete from backend
    try {
      await fetch(`http://localhost:5000/api/resources/${resourceId}`, {
        method: 'DELETE'
      })
    } catch (err) {
      console.error('Error deleting from backend:', err)
    }

    // Delete from IndexedDB if needed
    const resource = resourceFolders
      .find(f => f.subject === subjectName)
      ?.resources.find(r => r.id === resourceId)
    
    if (resource?.blobId) {
      await deleteBlob(resource.blobId)
    }

    setResourceFolders(prev =>
      prev.map(folder =>
        folder.subject === subjectName
          ? { ...folder, resources: folder.resources.filter(r => r.id !== resourceId) }
          : folder
      )
    )
  }

  const handleDownloadResource = async (resource: Resource) => {
    try {
      let dataUrl = resource.data
      
      // If stored in IndexedDB, retrieve it first
      if (resource.blobId && !dataUrl) {
        dataUrl = await getBlob(resource.blobId)
      }
      
      if (!dataUrl) {
        alert('Error: Could not retrieve file data')
        return
      }

      const link = document.createElement('a')
      link.href = dataUrl
      link.download = resource.name
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    } catch (error) {
      console.error('Error downloading file:', error)
      alert('Error downloading file')
    }
  }

  if (subjects.length === 0 || resourceFolders.length === 0) {
    return (
      <div className="w-full max-w-6xl mx-auto">
        <h1 className="text-3xl font-bold text-gray-800 mb-6"> Resources</h1>
        <div className="bg-gray-50 p-8 rounded-lg text-center">
          <p className="text-gray-500 text-lg">
            No subjects found. Please import a schedule first.
          </p>
        </div>
      </div>
    )
  }

  return (
    <div className="w-full max-w-6xl mx-auto">
      <h1 className="text-3xl font-bold text-gray-800 mb-6"> Resources</h1>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {resourceFolders.map(folder => (
          <div key={folder.subject} className="bg-white rounded-lg shadow-md p-6">
            <div className="flex items-center justify-between mb-4">
              <div className="flex items-center gap-3">
                <span className="text-3xl"></span>
                <h2 className="text-xl font-semibold">{folder.subject}</h2>
              </div>
              <span className="bg-blue-100 text-blue-800 px-3 py-1 rounded-full text-sm">
                {folder.resources.length}
              </span>
            </div>

            <div className="mb-4 max-h-64 overflow-y-auto">
              {folder.resources.length > 0 ? (
                <div className="space-y-2">
                  {folder.resources.map(resource => (
                    <div key={resource.id} className="flex items-center justify-between bg-gray-50 p-3 rounded">
                      <div className="flex items-center gap-2 flex-1">
                        <span>{resource.type === 'pdf' ? '' : ''}</span>
                        <div className="min-w-0">
                          <p className="text-sm truncate">{resource.name}</p>
                          <p className="text-xs text-gray-500">{resource.uploadedDate}</p>
                        </div>
                      </div>
                      <div className="flex gap-2">
                        <button onClick={() => handleDownloadResource(resource)} className="text-blue-600 text-lg hover:text-blue-800"></button>
                        <button onClick={() => handleDeleteResource(folder.subject, resource.id)} className="text-red-600 text-lg hover:text-red-800"></button>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-gray-500 text-sm">No resources</p>
              )}
            </div>

            <button
              onClick={() => {
                setSelectedSubject(folder.subject)
                setShowModal(true)
              }}
              className="w-full bg-blue-600 text-white py-2 rounded font-semibold hover:bg-blue-700"
            >
              + Add
            </button>
          </div>
        ))}
      </div>

      {showModal && selectedSubject && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl p-6 max-w-md w-full mx-4">
            <h2 className="text-2xl font-bold mb-4">Add Resources to {selectedSubject}</h2>

            <input
              type="file"
              multiple
              accept=".pdf,image/*"
              onChange={(e) => {
                const files = e.target.files
                if (files) {
                  const validFiles = Array.from(files).filter(f => {
                    const isPdf = f.type === 'application/pdf' || f.name.toLowerCase().endsWith('.pdf')
                    const isImage = f.type.startsWith('image/')
                    return isPdf || isImage
                  })
                  if (validFiles.length > 0) {
                    handleAddResource(validFiles)
                  }
                }
              }}
              className="w-full mb-4 p-2 border rounded"
            />

            <div className="flex gap-3">
              <button
                onClick={() => setShowModal(false)}
                className="flex-1 bg-gray-300 text-gray-800 py-2 rounded font-semibold hover:bg-gray-400"
              >
                Cancel
              </button>
              <button
                onClick={() => {
                  const fileInput = document.querySelector('input[type="file"]') as HTMLInputElement
                  fileInput?.click()
                }}
                className="flex-1 bg-blue-600 text-white py-2 rounded font-semibold hover:bg-blue-700"
              >
                Select Files
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
