import { useState, useEffect } from 'react'
import { format, addDays, startOfWeek, isSameDay, isAfter, isBefore, parseISO } from 'date-fns'

interface HomeworkItem {
  id: string
  date: string
  class_name: string
  homework_text: string
}

interface PendingTask {
  id: string
  task: string
  subject: string
  submissionDate: string
  done: boolean
}

const DEFAULT_SUBJECTS = ['Mathematics', 'Physics', 'Chemistry', 'English', 'History', 'Geography']

export default function WeeklyCalendar() {
  const [homeworkList, setHomeworkList] = useState<HomeworkItem[]>([])
  const [selectedDate, setSelectedDate] = useState<Date | null>(null)
  const [showForm, setShowForm] = useState(false)
  const [formData, setFormData] = useState({ className: '', homeworkText: '' })
  const [subjects, setSubjects] = useState<string[]>(DEFAULT_SUBJECTS)
  const [todaySchedule, setTodaySchedule] = useState<any[]>([])
  const [tomorrowSchedule, setTomorrowSchedule] = useState<any[]>([])
  const [pendingTasks, setPendingTasks] = useState<PendingTask[]>([])
  const [weekSchedule, setWeekSchedule] = useState<{ [key: string]: any[] }>({})

  // Load subjects from localStorage
  useEffect(() => {
    const savedSubjects = localStorage.getItem('subjects')
    if (savedSubjects) {
      try {
        setSubjects(JSON.parse(savedSubjects))
      } catch {
        setSubjects(DEFAULT_SUBJECTS)
      }
    }

    // Listen for storage changes
    const handleStorageChange = (e: StorageEvent) => {
      if (e.key === 'subjects' && e.newValue) {
        try {
          setSubjects(JSON.parse(e.newValue))
        } catch {
          setSubjects(DEFAULT_SUBJECTS)
        }
      }
      if (e.key === 'todaySchedule' && e.newValue) {
        try {
          setTodaySchedule(JSON.parse(e.newValue))
        } catch {
          setTodaySchedule([])
        }
      }
      if (e.key === 'tomorrowSchedule' && e.newValue) {
        try {
          setTomorrowSchedule(JSON.parse(e.newValue))
        } catch {
          setTomorrowSchedule([])
        }
      }
      if (e.key === 'pendingTasks' && e.newValue) {
        try {
          setPendingTasks(JSON.parse(e.newValue))
        } catch {
          setPendingTasks([])
        }
      }
    }

    window.addEventListener('storage', handleStorageChange)
    
    // Load schedule data from localStorage on mount
    const today = localStorage.getItem('todaySchedule')
    const tomorrow = localStorage.getItem('tomorrowSchedule')
    const pending = localStorage.getItem('pendingTasks')
    const week = localStorage.getItem('weekSchedule')
    
    if (today) {
      try {
        setTodaySchedule(JSON.parse(today))
      } catch {
        setTodaySchedule([])
      }
    }
    if (tomorrow) {
      try {
        setTomorrowSchedule(JSON.parse(tomorrow))
      } catch {
        setTomorrowSchedule([])
      }
    }
    if (pending) {
      try {
        setPendingTasks(JSON.parse(pending))
      } catch {
        setPendingTasks([])
      }
    }
    if (week) {
      try {
        setWeekSchedule(JSON.parse(week))
      } catch {
        setWeekSchedule({})
      }
    }

    return () => window.removeEventListener('storage', handleStorageChange)
  }, [])

  const today = new Date()
  const endDate = new Date(2026, 3, 30) // April 30, 2026
  const weeks: Date[][] = []
  
  let currentDate = startOfWeek(today)
  while (isBefore(currentDate, endDate)) {
    const weekStart = startOfWeek(currentDate)
    const week = Array.from({ length: 7 }, (_, i) => addDays(weekStart, i))
    weeks.push(week)
    currentDate = addDays(currentDate, 7)
  }

  const getHomeworkForDate = (date: Date) => {
    return homeworkList.filter(hw => 
      isSameDay(parseISO(hw.date), date)
    )
  }

  const getClassesForDate = (date: Date) => {
    const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
    const dayName = daysOfWeek[date.getDay()]
    
    // First try to get from week schedule
    if (weekSchedule && weekSchedule[dayName]) {
      return weekSchedule[dayName]
    }
    
    // Fallback to today/tomorrow schedule for backward compatibility
    const today = new Date()
    const tomorrow = new Date(today)
    tomorrow.setDate(tomorrow.getDate() + 1)

    if (isSameDay(date, today)) {
      return todaySchedule
    }
    if (isSameDay(date, tomorrow)) {
      return tomorrowSchedule
    }
    return []
  }

  const getPendingTasksForDate = (date: Date) => {
    return pendingTasks.filter(task => 
      isSameDay(parseISO(task.submissionDate), date)
    )
  }

  const handleAddHomework = () => {
    if (!selectedDate || !formData.className || !formData.homeworkText) {
      alert('Please fill all fields')
      return
    }

    const newHomework: HomeworkItem = {
      id: Date.now().toString(),
      date: format(selectedDate, 'yyyy-MM-dd'),
      class_name: formData.className,
      homework_text: formData.homeworkText
    }

    setHomeworkList([...homeworkList, newHomework])
    setFormData({ className: '', homeworkText: '' })
    setShowForm(false)
  }

  const handleDeleteHomework = (id: string) => {
    setHomeworkList(homeworkList.filter(hw => hw.id !== id))
  }

  return (
    <div className="w-full">
      <h1 className="text-3xl font-bold text-gray-800 mb-6"> Calendar - Weekly View</h1>
      
      {/* Current Month Display */}
      <div className="mb-6 flex items-center justify-between">
        <h2 className="text-xl font-semibold text-blue-600">
          {format(today, 'MMMM yyyy')}  April 2026
        </h2>
        <div className="text-sm text-gray-600">
          Total Homework: <span className="font-bold text-blue-600">{homeworkList.length}</span>
        </div>
      </div>

      {/* Week Grid */}
      <div className="space-y-6 max-h-[70vh] overflow-y-auto">
        {weeks.map((week, weekIndex) => (
          <div key={weekIndex} className="mb-8">
            <div className="grid grid-cols-7 gap-2">
              {week.map((date, dayIndex) => {
                const isToday = isSameDay(date, today)
                const isDisabled = isBefore(date, today)
                const homeworkForDate = getHomeworkForDate(date)
                const dayName = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
                const currentDayName = dayName[date.getDay()]

                return (
                  <div
                    key={dayIndex}
                    className={`p-4 rounded-lg border-2 min-h-[200px] flex flex-col ${
                      isToday
                        ? 'border-blue-500 bg-blue-50'
                        : isDisabled
                        ? 'border-gray-200 bg-gray-50 opacity-60'
                        : 'border-gray-200 bg-white hover:border-blue-300'
                    } transition-all`}
                  >
                    {/* Date Header */}
                    <div className="mb-3 pb-3 border-b border-gray-200">
                      <p className="text-sm font-semibold text-gray-600">{currentDayName}</p>
                      <p className={`text-2xl font-bold ${isToday ? 'text-blue-600' : 'text-gray-700'}`}>
                        {format(date, 'd')}
                      </p>
                      <p className="text-sm font-semibold text-gray-600">{format(date, 'MMM yyyy')}</p>
                    </div>

                    {/* Classes List */}
                    {(() => {
                      const classes = getClassesForDate(date)
                      return classes && classes.length > 0 ? (
                        <div className="mb-3 pb-3 border-b border-gray-100">
                          <p className="text-xs font-semibold text-green-700 mb-2"> Classes</p>
                          <div className="space-y-1">
                            {classes.map((cls, idx) => (
                              <div key={idx} className="bg-green-50 p-1.5 rounded border-l-3 border-green-500">
                                <p className="text-xs font-semibold text-green-800">{cls.time} - {cls.subject || cls.class}</p>
                              </div>
                            ))}
                          </div>
                        </div>
                      ) : null
                    })()}

                    {/* Pending Tasks List */}
                    {(() => {
                      const tasks = getPendingTasksForDate(date)
                      return tasks && tasks.length > 0 ? (
                        <div className="mb-3 pb-3 border-b border-gray-100">
                          <p className="text-xs font-semibold text-orange-700 mb-2"> Tasks Due</p>
                          <div className="space-y-1">
                            {tasks.map((task) => (
                              <div key={task.id} className={`bg-orange-50 p-1.5 rounded border-l-3 border-orange-500 ${task.done ? 'opacity-50' : ''}`}>
                                <p className={`text-xs font-semibold text-orange-700 ${task.done ? 'line-through' : ''}`}>{task.task}</p>
                                <p className="text-xs text-gray-600">{task.subject}</p>
                              </div>
                            ))}
                          </div>
                        </div>
                      ) : null
                    })()}

                    {/* Homework List */}
                    <div className="flex-1 mb-3">
                      {homeworkForDate.length > 0 ? (
                        <div className="space-y-2">
                          <p className="text-xs font-semibold text-blue-700 mb-2"> Homework</p>
                          {homeworkForDate.map(hw => (
                            <div key={hw.id} className="bg-blue-50 p-2 rounded border-l-4 border-blue-500">
                              <p className="text-xs font-semibold text-blue-700">{hw.class_name}</p>
                              <p className="text-xs text-gray-700 line-clamp-2">{hw.homework_text}</p>
                              <button
                                onClick={() => handleDeleteHomework(hw.id)}
                                className="text-xs text-red-600 hover:text-red-800 mt-1"
                              >
                                Delete
                              </button>
                            </div>
                          ))}
                        </div>
                      ) : (
                        <p className="text-xs text-gray-400">No homework</p>
                      )}
                    </div>

                    {/* Add Button */}
                    <button
                      onClick={() => {
                        setSelectedDate(date)
                        setShowForm(true)
                      }}
                      disabled={isDisabled}
                      className={`w-full py-1 px-2 text-sm font-medium rounded transition-colors ${
                        isDisabled
                          ? 'bg-gray-200 text-gray-400 cursor-not-allowed'
                          : 'bg-blue-500 text-white hover:bg-blue-600'
                      }`}
                    >
                      + Add HW
                    </button>
                  </div>
                )
              })}
            </div>
          </div>
        ))}
      </div>

      {/* Modal Form */}
      {showForm && selectedDate && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg p-6 w-full max-w-md shadow-xl animate-slide-up">
            <h3 className="text-2xl font-bold text-gray-800 mb-4">
              Add Homework - {format(selectedDate, 'MMM d, yyyy')}
            </h3>

            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Subject *
                </label>
                <select
                  value={formData.className}
                  onChange={(e) => setFormData({ ...formData, className: e.target.value })}
                  className="input-field"
                >
                  <option value="">Select a subject...</option>
                  {subjects.map((subject) => (
                    <option key={subject} value={subject}>
                      {subject}
                    </option>
                  ))}
                </select>
                <p className="text-xs text-gray-500 mt-1">
                  (Manage subjects in Settings)
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Homework Description *
                </label>
                <textarea
                  placeholder="Describe the homework assignment..."
                  value={formData.homeworkText}
                  onChange={(e) => setFormData({ ...formData, homeworkText: e.target.value })}
                  className="input-field h-24 resize-none"
                />
              </div>
            </div>

            <div className="flex gap-3 mt-6">
              <button
                onClick={() => {
                  setShowForm(false)
                  setFormData({ className: '', homeworkText: '' })
                }}
                className="flex-1 py-2 px-4 bg-gray-200 text-gray-800 rounded-lg hover:bg-gray-300 font-medium"
              >
                Cancel
              </button>
              <button
                onClick={handleAddHomework}
                className="flex-1 py-2 px-4 bg-blue-600 text-white rounded-lg hover:bg-blue-700 font-medium"
              >
                Add Homework
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
