import { useState } from 'react'
import MainLayout from '../components/Layout/MainLayout'
import Calendar from '../components/Calendar/WeeklyCalendar'
import Checklist from '../components/Checklist/ChecklistTab'
import Schedule from '../components/Schedule/ScheduleTab'
import Resources from '../components/Resources/ResourcesTab'
import '../styles/globals.css'

function App() {
  const [activeTab, setActiveTab] = useState('calendar')

  return (
    <MainLayout activeTab={activeTab} setActiveTab={setActiveTab}>
      <div className="p-6 animate-fade-in">
        {activeTab === 'calendar' && <Calendar />}
        {activeTab === 'checklist' && <Checklist />}
        {activeTab === 'schedule' && <Schedule />}
        {activeTab === 'resources' && <Resources />}
      </div>
    </MainLayout>
  )
}

export default App
