import { ReactNode, useState } from 'react'
import SettingsModal from './SettingsModal'

interface MainLayoutProps {
  children: ReactNode
  activeTab: string
  setActiveTab: (tab: string) => void
}

export default function MainLayout({ children, activeTab, setActiveTab }: MainLayoutProps) {
  const [showSettings, setShowSettings] = useState(false)
  const [showImportSchedule, setShowImportSchedule] = useState(false)

  const tabs = [
    { id: 'calendar', label: ' Calendar' },
    { id: 'checklist', label: ' Checklist' },
    { id: 'schedule', label: ' Schedule' },
    { id: 'resources', label: ' Resources' },
  ]

  const importScheduleFromExcel = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/schedule-data')
      const responseData = await response.json()
      
      // Ensure scheduleData is an array
      const scheduleData = Array.isArray(responseData) ? responseData : []
      
      if (!scheduleData || scheduleData.length === 0) {
        alert('No schedule data found')
        return
      }
      
      const today = new Date()
      const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
      
      const convertToScheduleItems = (dayData: any) => {
        if (!dayData) return []
        const items: any[] = []
        let id = 1
        
        // Iterate through all time slots
        Object.entries(dayData).forEach(([key, value]) => {
          const timeMatch = key.match(/\((\d{4})-(\d{4})\)/)
          if (timeMatch && value && key !== 'Day/Time ') {
            const startTime = `${timeMatch[1].slice(0, 2)}:${timeMatch[1].slice(2)}`
            items.push({
              id: `excel-${id++}`,
              time: startTime,
              class: String(value).split('(')[0].trim(),
              notes: String(value).includes('(') ? String(value) : ''
            })
          }
        })
        
        return items
      }

      // Extract unique subjects from all days
      const allSubjects = new Set<string>()
      
      // Parse all 7 days and store them
      const weekSchedule: { [key: string]: any[] } = {}
      let totalClasses = 0
      
      daysOfWeek.forEach((day) => {
        const dayData = scheduleData.find(item => item['Day/Time '] === day)
        if (dayData) {
          const items = convertToScheduleItems(dayData)
          weekSchedule[day] = items
          totalClasses += items.length
          
          // Collect subjects
          items.forEach(item => {
            if (item.class) {
              allSubjects.add(item.class)
            }
          })
        }
      })

      // Save full week schedule to localStorage
      localStorage.setItem('weekSchedule', JSON.stringify(weekSchedule))
      
      // Also save today and tomorrow for backward compatibility
      const todayDay = daysOfWeek[today.getDay()]
      const tomorrowDay = daysOfWeek[(today.getDay() + 1) % 7]
      
      if (weekSchedule[todayDay]) {
        localStorage.setItem('todaySchedule', JSON.stringify(weekSchedule[todayDay]))
      }
      if (weekSchedule[tomorrowDay]) {
        localStorage.setItem('tomorrowSchedule', JSON.stringify(weekSchedule[tomorrowDay]))
      }
      
      // Save extracted subjects to localStorage
      if (allSubjects.size > 0) {
        const existingSubjects = localStorage.getItem('subjects')
        let subjects = existingSubjects ? JSON.parse(existingSubjects) : []
        
        // Add new subjects that don't already exist
        allSubjects.forEach(subject => {
          if (!subjects.includes(subject)) {
            subjects.push(subject)
          }
        })
        
        localStorage.setItem('subjects', JSON.stringify(subjects))
      }
      
      alert(` Schedule imported!\n\nTotal classes: ${totalClasses} across all days`)
      setShowImportSchedule(false)
      // Trigger a page reload to update
      window.location.reload()
    } catch (error) {
      console.error('Import error:', error)
      alert('Error importing schedule: ' + (error as Error).message)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100">
      {/* Header */}
      <header className="bg-white shadow-md sticky top-0 z-40">
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
          <h1 className="text-2xl font-bold text-blue-600"> Class Routine Manager by Abrar Asif</h1>
          <div className="flex gap-2">
            <button
              onClick={() => setShowImportSchedule(true)}
              className="px-4 py-2 bg-purple-200 hover:bg-purple-300 rounded-lg font-medium transition-colors"
              title="Import schedule from Excel"
            >
               Import
            </button>
            <button
              onClick={() => setShowSettings(true)}
              className="px-4 py-2 bg-gray-200 hover:bg-gray-300 rounded-lg font-medium transition-colors flex items-center gap-2"
            >
               Settings
            </button>
          </div>
        </div>
      </header>

      {/* Navigation Tabs */}
      <nav className="bg-white border-b border-gray-200 sticky top-16 z-30">
        <div className="max-w-7xl mx-auto px-6 flex gap-1">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`px-4 py-3 font-medium transition-colors ${
                activeTab === tab.id
                  ? 'text-blue-600 border-b-2 border-blue-600'
                  : 'text-gray-600 hover:text-gray-900'
              }`}
            >
              {tab.label}
            </button>
          ))}
        </div>
      </nav>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-6 py-8">
        {children}
      </main>

      {/* Footer */}
      <footer className="bg-white border-t border-gray-200 mt-12">
        <div className="max-w-7xl mx-auto px-6 py-4 text-center text-gray-600 text-sm">
          <p>Class Routine Manager v1.0 by Abrar Asif | Running on localhost</p>
        </div>
      </footer>

      {/* Settings Modal */}
      <SettingsModal isOpen={showSettings} onClose={() => setShowSettings(false)} />

      {/* Import Schedule Modal */}
      {showImportSchedule && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg p-6 w-full max-w-md shadow-xl">
            <h2 className="text-2xl font-bold text-gray-800 mb-4"> Import Schedule</h2>
            <p className="text-gray-700 mb-6">
              Import your class schedule from the Excel file (schedule.xlsx)?
            </p>
            <p className="text-sm text-gray-600 mb-6">
              This will replace today and tomorrow's schedule with data from the Excel file.
            </p>
            <div className="flex gap-3">
              <button
                onClick={() => setShowImportSchedule(false)}
                className="flex-1 py-2 px-4 bg-gray-200 text-gray-800 rounded-lg hover:bg-gray-300 font-medium"
              >
                Cancel
              </button>
              <button
                onClick={importScheduleFromExcel}
                className="flex-1 py-2 px-4 bg-purple-600 text-white rounded-lg hover:bg-purple-700 font-medium"
              >
                Import
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
